import React from "react";
import { NavLink } from "react-router-dom";

class Navbar extends React.Component{

 
    render(){
        return(
            <nav id="navbar" className="navbar">
            <ul>
              <li><a className="nav-link scrollto active" href="/portfolio#hero">Home</a></li>
              <li><a className="nav-link scrollto" href="/portfolio#about">About</a></li>
              <li><a className="nav-link scrollto" href="portfolio#resume">Resume</a></li>
              <li><a className="nav-link scrollto" href="portfolio#services">Projects</a></li>
              <li><a className="nav-link scrollto" href="portfolio#contact">Contact</a></li>
              
            </ul>
            <i className="bi bi-list mobile-nav-toggle" />
          </nav>
        );
    }
}

export default Navbar;